
.symbol-info{
  margin : 0 24px 0 48px;
  display : flex;
  flex-direction: column;    
  .main {
    display : flex;
    align-items : center;
    height: 64px;
    justify-content: space-between;
    cursor: pointer;

    .s-left {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 60%;
      height: 100%;
      cursor: pointer;
      .market-area {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }
      
      .zone.DERI-Based {
        background: rgba(32, 59, 96, 0.5);
        border: 1px solid #203B60;
        border-top: 0px;
        border-bottom-right-radius: 8px;
        border-bottom-left-radius: 8px;
        text-align: center;
        font-size: 14px;
        line-height :22px;
      }
      
      .symbol-name {
        font-size : 24px;
        color : #E0ECFF;
        align-items: center;
        display: flex;
        white-space: nowrap;
        margin-bottom: 6px;
        img {
          margin-right: 8px;
        }
      }
      .select-icon {
        cursor: pointer;
        display: flex;
        align-items: center;
      }
      .divider-line {
        width: 1px;
        height: 32px;
        border-right: 1px solid #203B60;
      }
    }
    .kline-icon {
      display: none;
    }

    .index-area {
      display: flex;    
      justify-content: center;
      align-items: center;
      width: 90px;
      .index-area-wraper {
        text-align: center;
      }
      .rate {
        display: flex;
        align-items: center;
        img {
          width: 8px;
          margin-right: 4px;
        }
      }
      .rate.down {
        color : #CE5E5E;
      }
      .rate.up {
        color : #59AE99;
      }
      .index-value {
        font-size :24px;
        color : #E0ECFF;
        height: 32px;
      }
    }
  }
  .symbol-market.show {
    display: block;
  }
  .symbol-market {
    display: none;
    position: absolute;
    background: #203B60;
    z-index: 10;
    top : 66px;
    left: 0px;
  }
}

@media only screen and (max-width : 759px) {
  .symbol-info {
    margin : 0 24px 0 24px;
    .main {
      .kline-icon.hidden {
        visibility: hidden;
      }
      .kline-icon {
        display: block;
        visibility: visible;
      }
    }
  }
}
@media only screen and (min-width : 320px){
  .symbol-market.show{   
    width: 100%;
  }
}
@media only screen and (min-width : 760px){
  .symbol-market.show{   
    width: 404px;
  }
}