.simulate-pnl-modal {
  width: 720px;
  height: 605px;
  background: #09172C;
  font-weight: normal;

  .simulate-pnl-header {
    height: 77px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 24px;
    border-bottom: 1px solid #203B60;
    font-size: 22px;
    color: #e0ecff;
    font-weight: 600;

    img {
      cursor: pointer;
    }
  }

  .simulate-pnl-body {
    margin: 0 auto;
    margin-top: 30px;
    width: 645px;
    height: 395px;

    .association-pnl {
      display: flex;
      position: relative;

      .index-symbol,
      .mark-symbol {
        height: 125px;
        background: #182335;
        border-radius: 8px;
      }

      .m-quote-change {
        display: none;
      }

      .index-symbol {
        width: 196px;
        border: 1px dashed rgba(85, 119, 253, 0.5);
        padding: 16px 28px;

        .symbol-price {
          font-size: 26px;
          color: #FFFFFF;
          margin-top: 8px;
        }

        .quote-change {
          width: 80px;
          height: 23px;
          display: flex;
          padding: 0 4px;
          justify-content: space-around;
          align-items: center;
          font-size: 14px;
          font-weight: bold;
          margin-top: 8px;
          color: #59AE99;
          background: rgba(89, 174, 153, 0.2);
          border-radius: 6px;

        }

        .down {
          background: rgba(255, 86, 48, 0.2);
          color: #CE5E5E;
        }

        .zero {
          background: rgba(85, 119, 253, 0.3);
          color: #FFF;
        }
      }

      .mark-symbol {
        width: 429px;
        padding: 16px 48px;
        margin-left: 16px;

        .symbol-name {
          font-size: 22px;
        }

        .symbol-price {
          font-size: 38px;
          display: flex;
          align-items: center;
          color: #FFFFFF;
          margin-top: 16px;

          .mark-quote-change {
            min-width: 92px;
            min-height: 35px;
            padding: 0 8px;
            display: flex;
            justify-content: space-around;
            align-items: center;
            font-size: 14px;
            font-weight: bold;
            margin-left: 21px;
            color: #59AE99;
            background: rgba(89, 174, 153, 0.2);
            border-radius: 12px;
          }

          .down {
            background: rgba(255, 86, 48, 0.2);
            color: #CE5E5E;
          }

          .zero {
            background: rgba(85, 119, 253, 0.3);
            color: #FFF;
          }
        }
      }

      .association-logo {
        position: absolute;
        left: 150px;
        top: 10px;
      }
    }

    .chart {
      width: 100%;
      height: 245px;
      margin-top: 25px;
      position: relative;

      .long-short {
        position: absolute;
        top: -4px;
        right: 0;
        display: flex;
        width: 125px;
        height: 24px;
        border-radius: 6px;
        align-items: center;
        justify-content: space-between;
        font-size: 10px;
        .long,.short{
          width: 50%;
          height: 100%;
          padding: 0 12px;
          border: 1px solid #203B60;
          display: flex;
          justify-content: space-between;
          align-items: center;
          cursor: pointer;
          font-weight: 500;
          img{
            margin-top: 4px;
          }
        }
        .long{
          border-right: 1px solid transparent;
          border-radius: 6px 0 0 6px;
        }
        .short{
          border-left: 1px solid transparent;
          border-radius: 0 6px 6px 0;
        }
        .long.check{
          border: 1px solid #5577FD;
          color: #FFF;
        }
        .short.check{
          border: 1px solid #5577FD;
          color: #FFF;
        }

      }

      .loading {
        width: 30px;
        height: 30px;
      }

      @keyframes circle {
        0% {
          transform: rotate(0);
        }

        100% {
          transform: rotate(360deg);
        }
      }

      .max,
      .min {
        font-size: 14px;
        color: #FFF;
        font-weight: 600;
      }

      .max {
        margin-bottom: 4px;
      }

      .min {
        margin-top: 4px;
      }

      .chart-box {
        width: 100%;
        height: 200px;
      }

      .recharts-tooltip-wrapper {
        width: 175px;
        height: 66px;
        visibility: visible !important;
      }

      .tip-chart {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 175px;
        height: 66px;
        background-color: #203B60;
        border-radius: 8px;
        font-size: 10px;
        padding: 0 8px;

        .tip-index-price,
        .tip-market-price {
          font-size: 12px;
          font-weight: bold;
          margin: 4px 0;
          color: #FFF;
        }

        .tip-market-pnl,
        .tip-index-pnl {
          color: #59AE99;

          img {
            margin-right: 4px;
          }
        }

        .down {
          color: #CE5E5E;
        }
      }

      .default {
        position: absolute !important;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  .simulate-pnl-footer {
    width: 654px;
    height: 64px;
    border: 1px solid #203B60;
    margin: 0 auto;
    margin-top: 16px;
    border-radius: 6px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 72px 0 36px;
    color: #FFFFFF;
    font-size: 18px;

    .market {
      font-size: 20px;
    }

    .symbol-name,
    .mark-name {
      font-size: 14px;
      color: #FFFFFF;
      opacity: 0.5;
    }
  }
}

@media only screen and (max-width : 759px) {
  .simulate-pnl-modal {
    margin-top: 150px;
    width: 323px;
    height: 754px;
    border-radius: 16px;
    border: 1px solid #203B60;

    .simulate-pnl-header {
      height: 54px;
    }

    .simulate-pnl-body {
      width: 281px;
      height: 504px;

      .association-pnl {
        flex-wrap: wrap;

        .index-symbol {
          width: 281px;
          height: 80px;
          padding: 11px 24px;

          .symbol-price {
            display: flex;
            align-items: center;
            margin-top: 4px;
          }

          .quote-change {
            display: none;
          }

        }

        .mark-symbol {
          width: 281px;
          margin-left: 0px;
          margin-top: 11px;
          padding: 18px 24px;

          .symbol-price {
            margin-top: 8px;
          }
        }

        .m-quote-change {
          min-width: 68px;
          min-height: 23px;
          display: inline-block;
          justify-content: space-around;
          align-items: center;
          font-size: 14px;
          font-weight: bold;
          padding: 0 4px;
          color: #59AE99;
          background: rgba(89, 174, 153, 0.2);
          border-radius: 6px;
          text-align: center;
          line-height: 23px;
          margin-left: 24px;

          img {
            margin-right: 8px;
          }
        }


        .association-logo {
          left: 105px;
          top: 50px;

          img {
            width: 72px;
            height: 72px;
          }
        }
      }
    }

    .simulate-pnl-footer {
      width: 281px;
      height: 133px;
      flex-wrap: wrap;
      padding-left: 18px;
    }
  }
}