.network-hint-box {
  border-top: 1px solid #203B60;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(206, 94, 94, 0.1);
  color: #FFF;
  font-size: 18px;
  min-width: 375px;
  padding:8px 24px;
  .hint-img{
    display: flex;
  }
  img {
    margin-right: 16px;
  }
  .network-name{
    color: #CD7A37;
    cursor: pointer;
  }
  .network-name::after{
    content: 'or';
    color: #FFF;
  }
  .network-name:last-child::after{
    content: '';
  }
  .switch-btn {
    width: 184px;
    height: 32px;
    background: #3756CD;
    border-radius: 4px;
    display: flex;
    align-items: center;
    cursor: pointer;
    justify-content: center;
    margin-left: 48px;
  }
}
@media only screen and (max-width : 760px){
  .network-hint-box{
    flex-direction: column;
    height: 140px;
    .switch-btn{
      margin-top: 8px;
    }
  }
}
