@media only screen and (min-width : 1140px){
  .m-history {
    .h-body {
      font-size: 14px;
    }
  }
}
@media only screen and (max-width : 760px){
  .m-history {
    .h-header {
      .h-col {
        min-width: 112px;
      }
    }
    .h-body {
      .h-col {
        min-width: 112px;
      }
    }
  }

  .fixed-column {
    display: none;
  }
}
.his-container {
  position: relative;
  .fixed-column {
    width: 40px;
    .f-body {
      height: 184px;
    }
  }
  .m-history {
    margin-top : 16px;
    overflow: auto;
    white-space: nowrap;
    height: 242px;
    font-size: 12px;
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
    position: relative;
    &::-webkit-scrollbar { 
      display: none;  /* Safari and Chrome */
    }

    .fixed-column {
      width: 40px;
      .f-body {
        height: 188px;
        .columns {
          margin : 0;
        }
      }
    }
    .h-header {
      display : flex;
      justify-content: space-between;
      height: 40px;
      align-items : center;
      padding-left: 18px;
      font-size: 14px;
      background-color: #1c2738;
      position: sticky;
      top: 0;
    }
    .h-body {
      color: #E0ECFF;
      height : 184px;
      overflow: auto;
      font-weight: 500;

      -ms-overflow-style: none;  /* Internet Explorer 10+ */
      scrollbar-width: none;  /* Firefox */
      &::-webkit-scrollbar { 
        display: none;  /* Safari and Chrome */
      }
      .no-data {
        margin: auto;
        margin-top: 48px;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 14px;
        font-weight: 600;
        position: absolute;
        left: 44%;
        color: #93A1B8;
      }

      .h-row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-left: 18px;
        height: 32px;
        margin-bottom: 8px;
        margin-top: 10px;
      }
      .h-row.loading-line {
        width: 100%;
        margin-bottom: 8px;
        height: 12px;
      }
    }
    .h-col.Sell {
      color : #CE5E5E;
    }
    .h-col.Buy {
      color : #59AE99;;
    }
    .h-col {
      margin-right : 16px;
      width: 130px;
    }
    .h-col.time {
      div {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
      }
    }
  }
}