.position-container.fixed .p-col:last-child {
  visibility: hidden;
}
.position-container {
  position: relative;
  .m-position {
    margin-top: 16px;
    font-size: 14px;
    overflow: auto;
    position: relative;
    white-space: nowrap;
    background-color: #0A172B;
    -ms-overflow-style: none;
    /* Internet Explorer 10+ */
    height: 242px;
    scrollbar-width: none;
    display: flex;
    flex-direction: column;
  
    .fixed-column {
      display: block;
    }
    
  
    /* Firefox */
    &::-webkit-scrollbar {
      display: none;
      /* Safari and Chrome */
    }
  
    .p-footer {
      height: 32px;
      display: flex;
      align-items: center;
      position: sticky;
      left: 0;
      bottom: 0; 
      margin-top: 8px;
      scrollbar-width: none;
      overflow: auto;
      &::-webkit-scrollbar {
        display: none;
      }
      .liq-label {
        padding: 8px 18px;
        background: #1c2738;
        border-radius: 6px;
      }
      .liq-item {
        padding: 0 16px;
        border-left: 1px solid #203b60;
      }
      .liq-item:first-child {
        border: 0px;
      }
      span {
        div {
          display: inline-block;
        }
      }
      .split-line {   
        border-left: 1px solid #203b60;
        margin: 0 12px;
      }
    }
  
    .p-header {
      position: sticky;
      top: 0;
      display: flex;
      justify-content: space-between;
      height: 40px;
      align-items: center;
      background-color: #1c2738;
      border-radius: 4px;
      padding-left: 18px;
    }
    .p-body {
      font-size: 12px;
      font-weight: 500;
      position: relative;
      .main {
        height: 144px;
        scrollbar-width: none;
        overflow: auto;
        /* Firefox */
        &::-webkit-scrollbar {
          display: none;
          /* Safari and Chrome */
        }
      }
      .no-data {
        // margin: auto;
        margin-top: 48px;
        display: flex;
        flex-direction: column;
        align-items: center;
        font-weight: 600;
        font-size: 14px;
        position: absolute;
        left: 44%;
      }
    }
  
    .p-row {
      display: flex;
      justify-content: space-between;
      color: #E0ECFF;
      align-items: center;
      height: 32px;
      margin-bottom: 8px;
      margin-top: 10px;
      padding-left: 18px;
    }
  
    .p-row.selected{
      border : 1px solid #CE5E5E;
      background: linear-gradient(168.53deg, rgba(34, 53, 84, 0.3) -40.23%, rgba(205, 201, 241, 0.3) 57.23%);
      width : 100%;
      .p-col {
        @keyframes strike{
          0%   { width : 0; }
          100% { width: 100%; }
        }
        .strike {
          position: relative;
          width: max-content;
          &::after {
            content: ' ';
            position: absolute;
            top: 50%;
            left: 0;
            width: 20%;
            height: 2px;
            background: #CE5E5E;
            animation-name: strike;
            animation-duration: .8s;
            animation-timing-function: linear;
            animation-iteration-count: 1;
            animation-fill-mode: forwards; 
          }
        }
      }
      .p-col:first-child {
        padding-left: 18px;
      }
    }
    .p-row.recover {
      border: 1px solid #CE5E5E;
      background: linear-gradient(168.53deg, rgba(34, 53, 84, 0.3) 0%, rgba(205, 201, 241, 0.3) 97.46%);
      width: 100%;
      -moz-filter: blur(15px);
      -o-filter: blur(15px);
      -ms-filter: blur(15px);
      filter: blur(3px);
    }
    .trans-error-tip {
      text-align: center;
      color: #FFF;
      font-size: 12px;
      width: 100%;
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 32px;
    }
    .p-row.selected .p-col.close-position div,.p-row.recover .p-col.close-position div {
      background-color: transparent;
    }
  
    .p-col.symbol {
      cursor: pointer;
    }
  
    .p-row.loading-line {
      width: 100%;
      height: 12px;
    }
  
    .p-col.red {
      color: #CE5E5E;
    }
  
    .p-col.green {
      color: #59AE99;
    }
    // .p-col:last-child {
    //   position: sticky;
    //   right: 0;
    // }
  
    // .p-col:last-child::after {
    //   position: absolute;
    //   top: 0;
    //   bottom: -1px;
    //   left: -1px;
    //   width: 20px;
    //   transform: translateX(-100%);
    //   transition: box-shadow .3s;
    //   content: "";
    //   pointer-events: none;
    //   box-shadow: inset -10px 0 8px -8px rgb(159, 161, 159);
    // }
  
    .unrealizedPnl {
      display: flex;
      align-items: center;
      justify-content: space-between;
      img {
        margin-left: 4px;
        margin-right: 16px;
      cursor: pointer;
      }
    }
  
    .p-col {
      min-width: 130px;
      margin-right: 16px;
  
      .red {
        color: #CE5E5E;
      }
  
      .green {
        color: #59AE99;
      }
  
      .gray {
        color: #93A1B8;
      }
    }
  
    .p-col.close-position {
      cursor: pointer;
    }
  }
}
