
.fixed-column.scrolled {
  width: auto;
  .container::after,.container .f-body::after {
    position: absolute;
    top: 0;
    bottom: -1px;
    left: -1px;
    width: 1px;
    transform: translateX(-100%);
    transition: box-shadow .3s;
    content: "";
    pointer-events: none;
    box-shadow: -8px 0px 17px rgba(6, 17, 35, 0.69);
  }
  .f-header {
    .f-col {
      display: block;
      visibility: visible;
      opacity: 1;
      // transition: visibility 0.3s linear,opacity 0.3s linear;
    }
  }
}
.fixed-column {
  position: absolute;
  right: 0;
  top: 0;
  background: #0A172B;
  .arrow {
      position: absolute;
      top: calc( 50% - 24px);
      left: 20px;
      img {
        cursor: pointer;
        margin-top: 10px;
      }
    }
  .container {
    background : linear-gradient(to left, rgba(10, 23, 43, 0) 0%, rgba(10, 23, 43, 0.34) 40.31%, rgb(32 59 96 / 10%) 84.88%);
    
  }
  

  .f-header {
    position: sticky;
    top: 0;
    height: 40px;
    line-height: 40px;
    color : #93A1B8;
    border-radius: 4px;
    text-align: right;
    .f-row {
      display: flex;
      justify-content: flex-end;
    }
    .f-col {
      background: #1c2738;
      margin: 0 10px;
      border-radius: 6px;
      line-height: 40px;
      width: 60px;
      text-align: center;
      font-size: 14px;
      font-weight: 600;
      color: #93A1B8;
    }
  }
  .f-body {
    height: 144px;
    scrollbar-width: none;
    overflow: auto;
    &::-webkit-scrollbar {
      display: none;
    }
    .columns {
      margin: 0 10px 0 60px;
      .f-row {
        line-height: 32px;
        height: 32px;
        margin: 10px 0 8px; 
        .f-col {
          cursor: pointer;
        }
      }
    }
  }
}
.fixed .fixed-column {
  display: block;
}
@media screen and (min-width: 375px) and (max-width : 760px){ 
  .fixed-column {
    .container::after {
      width: 0;
    }
    .arrow {
      display: none;
    }
    .container {
      .f-body {
        .columns {
          margin: 0 10px 0 10px;
        }
      }
    }
  } 
}