.trade-info-content {
  display : flex;
  height: 64px;
  align-items: center;
  justify-content: space-between;
  padding: 0 32px;
  overflow-x: auto;
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  scrollbar-width: none;

  /* Firefox */
  &::-webkit-scrollbar {
    display: none;
    /* Safari and Chrome */
  }

  .m-body {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .m-col {
      display: flex;
      margin-right: 48px;
      .main {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        justify-content: space-between;
        white-space: nowrap;
        height: 46px;

        // align-items: center;
        .col-value {
          font-size: 20px;
          color: #E0ECFF;
          height: 24px;
          white-space: nowrap;
          img{
            margin-left: 4px;
            cursor: pointer;
          }
          .funding-rate {
            display: inline-block;
          }
        }
        .calculator{
          display: flex;
          align-items: center;
          .text{
            border-bottom: 0;
          }
        }

        .col-value.up {
          color: #59AE99;
        }

        .col-value.down {
          color: #CE5E5E;
        }

        .col-value {
          .up {
            color: #59AE99;
          }
        }

        .col-value {
          .down {
            color: #CE5E5E;
          }
        }
      }
      .right-side {
        display: flex;
        align-items: center;
        font-size: 20px;
        // margin-left: 16px;
        height: 48px;

        .power-funding-desc {
          cursor: pointer;
        }
        .pdfd.show {
          display: flex;
        }
        .pdfd {
          display: none;
          background: #222021;
          color: #FFB110;
          border-radius: 4px;
          margin-left: 28px;
          .label {
            color: #93A1B8;
            font-size: 16px;
            // padding: 4px;
          }
          .light {
            color  : #E0ECFF;
            font-weight: 600;
          }
          .pdfd-right {
            // margin-left: 16px;
            padding: 8px;
            .simulate {
              display: flex;
              white-space: nowrap;
              .up {
                color: #59AE99;
                margin-left: 4px;
              }
              .down {
                margin-left: 4px;
                color: #CE5E5E;
              }
              .before {
                .unit {
                  color: #959D9B;
                }
                input{
                  color: #DFE9E6;
                }
              }
              .simulate-input {
                width: 48px;
                margin: 0 8px;
                input {
                  width: 36px;
                  text-align: center;
                  padding: inherit;
                }
                .unit {
                  margin-right: 4px;
                }
              }
            }
          }
        }
      }
    }
  }

  .slide-icon {
    display: none;
  }
}

@media only screen and (max-width : 759px) {
  .trade-info-content {
    padding: 0 24px 0 24px;

    .slide-icon {
      position: absolute;
      display: block;
      background: #0A172B;
      height: 94px;
      display: flex;
      align-items: center;
    }

    .slide-icon.right {
      right: 0px;
    }

    .slide-icon.left {
      left: 0px;
    }
  }
}