.share-modal{
  width: 869px;
  height: 584px;
  .share-img{
    width: 100%;
    height: 528px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .share-btn{
    width: 100%;
    height: 56px;
    background: #0D1629;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 18px;
    .close-share{
      cursor: pointer;
    }
    .copy-twitter{
      display: flex;
      cursor: pointer;
      .share-twitter{
        margin-right: 8px;
      }
    }
  }
}

@media only screen and (max-width : 869px){
  .share-modal{
    width: 375px;
    height: 284px;
    .share-img{
      height: 228px;
    }
  }
}