  .trade-guide {
    width: 313px;
    height: 114px;
    background-color: #102140 !important;
    border-radius: 4px;
    padding: 8px 16px !important;

    .step-one {
      color: #E0ECFF;
      width: 270px;
      font-weight: 400;
      font-size: 20px;
      position: absolute;
      top: 7px;
    }

    div[dir='ltr']{
      div{
        display: none;
      }
      position: absolute;
      top: 71px;
      right: 16px;
      margin-top: 0;
      svg {
        display: none;
      }
      .next {
        width: 120px;
        height: 32px;
        background: #3756CD;
        border-radius: 4px;
        font-weight: 700;
        font-size: 18px;
        color: #FFFFFF;
        cursor: pointer;
        border: 0;
      }
      .prev{
        color: #3756CD;
        border: 0;
        background-color: transparent;
        margin-right: 16px;
        font-size: 18px;
        font-weight: 700;
        cursor: pointer;
      }
    }

    .step-num {
      position: absolute;
      top: 76px;
      font-size: 20px;
      font-weight: 400;

      .now-step {
        color: #E0ECFF;
      }
    }

    .close-modal {
      position: absolute;
      right: 16px;
      top: 12px;
      width: 12px;
      height: 12px;
      cursor: pointer;
    }
  }