a {
  text-decoration: none;
  color: #E0ECFF;
}
.trade-layout {
  display: grid;
  grid-template-columns: 404px 1fr;
  grid-template-rows: 64px 496px auto auto;
  grid-template-areas: "symbol-area trade-info trade-info" "account-info kline kline" "orderbar kline kline" "orderbar my-info my-info" "orderbar contract contract";
  gap: 0px;
  border-top: 1px solid #203B60;
  position: relative;
}
.trade-layout .orderbar {
  grid-area: orderbar;
  border-right: 1px solid #203B60;
  position: relative;
}
.trade-layout .orderbar.retired .retired-trade-mask {
  cursor: not-allowed;
  position: absolute;
  width: 100%;
  height: 100%;
}
.trade-layout .orderbar.retired .trade-warnning.show .content {
  font-size: 14px!important;
  font-weight: bold;
}
.trade-layout .account-info {
  position: relative;
  grid-area: account-info;
  border-right: 1px solid #203B60;
}
.trade-layout .symbol-area {
  grid-area: symbol-area;
  border-right: 1px solid #203B60;
  z-index: 10;
}
.trade-layout .trade-info {
  grid-area: trade-info;
  height: 64px;
  overflow: hidden;
}
.trade-layout .kline {
  grid-area: kline;
  border-top: 1px solid #203B60;
}
.trade-layout .my-info {
  grid-area: my-info;
  border-top: 1px solid #203B60;
  z-index: 1;
  overflow: hidden;
}
.trade-layout .contract {
  grid-area: contract;
  border-top: 1px solid #203B60;
  border-right: 1px solid #203B60;
}
.trade-layout .trades {
  grid-area: trades;
  border-top: 1px solid #203B60;
}
@media only screen and (min-width: 0px) {
  .trade-layout {
    grid-template-columns: 380px 1fr;
    grid-template-areas: "symbol-area" "trade-info" "account-info" "orderbar" "my-info" "contract";
    grid-template-rows: auto ;
  }
  .trade-layout.mobile-trade-layout {
    grid-template-areas: "symbol-area" "kline" "orderbar" "my-info" "contract";
  }
  .trade-layout.mobile-trade-layout .kline {
    display: block;
  }
  .trade-layout.mobile-trade-layout .account-info,
  .trade-layout.mobile-trade-layout .trade-info {
    display: none;
  }
  .symbol-area {
    border-right: 0;
    border-bottom: 1px solid #203B60;
    position: sticky;
    top: 0;
    background: #0A172B;
  }
  .kline {
    display: none;
    border-top: 0;
  }
  .orderbar {
    border-right: 1px solid #203B60;
  }
  .account-info,
  .trade-info {
    display: block;
  }
}
@media only screen and (min-width: 375px) {
  .trade-layout.mobile-trade-layout {
    grid-template-areas: "symbol-area" "kline" "orderbar" "my-info" "contract";
  }
  .trade-layout.mobile-trade-layout .kline {
    height: auto;
  }
  .trade-layout.mobile-trade-layout .account-info {
    height: 0;
  }
  .trade-layout {
    grid-template-columns: 100%;
    grid-template-areas: "symbol-area" "trade-info" "account-info" "orderbar" "my-info" "contract";
    grid-template-rows: 64px 1fr;
  }
  .trade-layout .kline {
    border-top: 0px;
    height: 0;
  }
  .trade-layout .symbol-area {
    border-right: 0;
    border-bottom: 1px solid #203B60;
  }
  .trade-layout .orderbar {
    border-right: 0;
  }
  .trade-layout .contract {
    border-right: 0;
  }
  .trade-layout .account-info {
    height: initial;
    border-right: 0px;
  }
}
@media only screen and (min-width: 760px) {
  .trade-layout {
    grid-template-columns: 404px 1fr;
    grid-template-rows: 64px auto 178px 334px auto;
    grid-template-areas: "symbol-area trade-info trade-info" "account-info kline kline" "orderbar kline kline" "orderbar my-info my-info" "orderbar contract contract";
  }
  .trade-layout .symbol-area {
    border-right: 1px solid #203B60;
    border-top: 0;
    border-bottom: 0;
    position: relative;
  }
  .trade-layout .kline {
    display: block;
    border-top: 1px solid #203B60;
    height: inherit;
  }
  .trade-layout .trade-info {
    border-top: 0px;
  }
  .trade-layout .orderbar {
    border-right: 1px solid #203B60;
  }
  .trade-layout .contract {
    border-top: 1px solid #203B60;
    border-right: 1px solid #203B60;
  }
  .trade-layout .trades {
    border-top: 1px solid #203B60;
  }
  .trade-layout .account-info {
    height: initial;
    border-right: 1px solid #203B60;
  }
}
@media only screen and (min-width: 1140px) {
  .trade-layout {
    grid-template-columns: 404px 1fr;
    grid-template-rows: 64px auto 178px 334px auto;
    grid-template-areas: "symbol-area trade-info trade-info" "account-info kline kline" "orderbar kline kline" "orderbar my-info my-info" "orderbar contract contract";
  }
  .trade-layout .symbol-area {
    border-right: 1px solid #203B60;
    border-top: 0;
    border-bottom: 0;
  }
  .trade-layout .kline {
    display: block;
  }
  .trade-layout .orderbar {
    border-right: 1px solid #203B60;
  }
  .trade-layout .contract {
    border-top: 1px solid #203B60;
    border-right: 1px solid #203B60;
  }
  .trade-layout .trades {
    border-top: 1px solid #203B60;
  }
  .trade-layout .account-info {
    border-right: 1px solid #203B60;
  }
}
@media only screen and (min-width: 1568px) {
  .trade-layout {
    grid-template-columns: 404px 1fr;
    grid-template-rows: 64px minmax(307px, auto) 178px 334px auto;
    grid-template-areas: "symbol-area trade-info trade-info" "account-info kline kline" "orderbar kline kline" "orderbar my-info my-info" "orderbar contract contract";
  }
  .trade-layout .symbol-area {
    border-right: 1px solid #203B60;
    border-top: 0;
    border-bottom: 0;
  }
  .trade-layout .kline {
    display: block;
  }
  .trade-layout .orderbar {
    border-right: 1px solid #203B60;
  }
  .trade-layout .contract {
    border-top: 1px solid #203B60;
    border-right: 1px solid #203B60;
  }
  .trade-layout .trades {
    border-top: 1px solid #203B60;
  }
  .trade-layout .account-info {
    border-right: 1px solid #203B60;
  }
}
@media only screen and (min-width: 2560px) {
  .trade-layout {
    grid-template-columns: 404px 1fr;
    grid-template-rows: 64px auto 178px 334px auto;
    grid-template-areas: "symbol-area trade-info trade-info" "account-info kline kline" "orderbar kline kline" "orderbar my-info my-info" "orderbar contract contract";
  }
  .trade-layout .symbol-area {
    border-right: 1px solid #203B60;
    border-top: 0;
    border-bottom: 0;
  }
  .trade-layout .kline {
    display: block;
  }
  .trade-layout .orderbar {
    border-right: 1px solid #203B60;
  }
  .trade-layout .orderbar .leverage-bar {
    min-height: 718px;
  }
  .trade-layout .contract {
    border-top: 1px solid #203B60;
    border-right: 1px solid #203B60;
  }
  .trade-layout .trades {
    border-top: 1px solid #203B60;
  }
  .trade-layout .account-info {
    height: initial;
    border-right: 1px solid #203B60;
  }
}
