.trade-mask {
  width: 100%;
  height: 100%;
  min-height: 206px;
  position: absolute;
  z-index: 9;
  background: linear-gradient(180deg, rgba(10, 23, 43, 0.13) 0%, #0A172B 68.07%);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: center;
  align-items: center;

  .trade-mask-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;

    .text-trade-mask {
      font-weight: 600;
      font-size: 18px;
      color: #FFFFFF;
      margin-top: 21px;
      margin-bottom: 42px;
      text-align: center;
    }

    .network-name-box {
      font-weight: 400;
      font-size: 16px;
      color: #E0ECFF;
    }

    .network-name {
      color: #CD7A37;
      cursor: pointer;
    }

    .network-name::after {
      content: 'or';
      color: #FFF;
    }

    .network-name:last-child::after {
      content: '';
    }

    .btn-trade-mask {
      width: 127px;
      height: 40px;
      background: #5577FD;
      border-radius: 1000px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #FFFFFF;
      cursor: pointer;
      font-weight: 700;
      font-size: 14px;
    }
  }
}

.position-container ,.his-container{
  .trade-mask {
    .trade-mask-info {
      flex-direction: inherit;

      .text-trade-mask {
        margin: 0;
        margin-left: 10px;
        margin-right: 35px;
        text-align: left;
      }
    }
  }
}